/* Toggle Switch */

/* The switch - the box around the slider */
.toggleSwitch {
	position: relative;
	max-width: 60px;
	justify-self: center;
}

/* Hide default HTML checkbox */
.toggleSwitch input.check {
	display: none;
}

/* The slider */
.toggleSlider {
	position: relative;
	cursor: pointer;
	top: 0;
	left: 0;
	right: 0;
	bottom: 0;
	width: 80px;
	background-color: #ccc;
	transition: .4s;
	min-height: 20px;
	display: flex;
}

.toggleSlider:before {
	position: absolute;
	content: "";
	height: 100%;
	width: 30.8%;
	left: 4px;
	background-color: white;
	transition: .4s;
}

input.check:checked+.toggleSlider {
	background-color: #2196F3;
}

input.check:focus+.toggleSlider {
	box-shadow: 0 0 1px #2196F3;
}

input.check:checked+.toggleSlider:before {
	left: calc(100% - 30.8% - 4px);
}

/* Rounded sliders */
.toggleSlider.round {
	border-radius: 34px;
}

.toggleSlider.round:before {
	border-radius: 34px;
}

.switchContainer label div:not(:first-child) {
	font-size: 14px;
	font-weight: 300;
}
.switchContainer {
	border-radius: 15px;
}
.switchContainer:hover > * {
	background-color: transparent;
}
