.container {
    max-width: 1180px;
	box-sizing: border-box;
	overflow: hidden;

	display: grid;
	column-gap: 25px;
	row-gap: 25px;
	grid-template-columns: 1fr 1fr;
	font-family: 'Josefin Sans', sans-serif;
}

.section {
	display: grid;
	overflow: hidden;
}

@media only screen and (max-width: 700px) {
	div.container {
		grid-template-columns: 100%;
	}
	div.section {
		grid-column: unset;
		grid-row: unset;
	}
	div.title {
		grid-column: unset;
	}
}

.title {
	grid-column: 1 / span 2;
	grid-row: 1 / span 1;
}
.title h1 {
	margin: 20px 0;
}
.title h3 {
	margin: 0;
}

@media only screen and (max-width: 700px) {
	.container {
		grid-template-columns: 1fr;
	}
	.section > table {
		margin-bottom: 0px;
	}
	.section > table thead {
		display: table-header-group;
	}
	.section > table th {
		width: 100%;
		display: flex;
	}
	.section > table td {
		width: 100%;
		display: flex;
		text-align: center;
	}
}
.sectionHeader {
	background-color: rgba(135, 207, 235, 0.5);
	justify-content: center;
	text-align: center;
	box-sizing: border-box;
	border-radius: 15px;
	overflow: hidden;
}

.expando {
	cursor: pointer;
}

.info {
	display: grid;
	grid-template-columns: auto 1fr auto;
	row-gap: 15px;
	grid-template-areas:
		'Name . Buttons'
		'Details . Buttons'
		'modes modes modes'
}

.info > h1 {
	display: flex;
	align-items: flex-end;
	margin-block-end: 0em;
}
.buttons {
	grid-area: Buttons;
}
.info .buttons {
	display: grid;
	grid-auto-rows: 2em;
	grid-auto-flow: row;
	row-gap: 12px;
	transform: scaleY(-1);
	justify-content: right;
	margin-top: calc(12px + 1em + 20px);
}
.info .buttons > .row {
	overflow: hidden;
	display: grid;
	grid-auto-columns: auto;
	grid-auto-flow: column;
	column-gap: 12px;
	transform: scaleY(-1);
	width: max-content;
	justify-self: right;
}
.buttons > .row > div, .buttons > .row > a {
	padding: 0 20px;
	border-radius: 7px;
}
.details {
	grid-area: Details;
	display: grid;
	align-items: end;
}
.details > div {
	height: max-content;
}
.flag {
	display: flex;
	height: 50px;
	margin: 16px 0;
	align-items: center;
}
.flag > img {
	width: unset;
	max-width: unset;
	max-height: 50px;
	height: 100%;
	border-radius: 5px;
	margin-right: 20px;
}

section.accountSection:not(:first-of-type) {
	margin-top: 5px;
}
section.accountSection:first-of-type {
	margin-top: 25px;
}
.optionsContainer {
	grid-template-columns: 31.8% 31.8%;
}
.optionsContainer > *:first-child:nth-last-child(2) {
	grid-column: span 2;
}
.pendingContainer {
	grid-template-columns: 1fr auto;
}
.pendingContainer > div:first-child:last-child {
	grid-column: 1 / -1;
}
div.account {
	border-radius: 7px;
}
div.claim {
	cursor: default;
	padding: 10px;
	width: max-content;
}
a.account {
	display: grid;
	grid-template-columns: repeat(3, auto);
	column-gap: 5px;
	justify-content: center;
	align-items: center;
	grid-auto-flow: column;
}
.account.inline > img.icon, .account.inline > svg.icon {
	height: 40px;
	width: 40px;
}
.account.inline, .account.inline:active {
	box-sizing: border-box;
	grid-template-columns: unset;
	border-radius: 15px;
	column-gap: 10px;
	cursor: pointer;
}
.account.inline:global(.sent,) .account.inline:global(.sent).active, .account.inline:global(.sent):hover {
	cursor: default;
}
.account.inline > span, .facebookLink, .facebookLink > button {
	background-color: transparent;
	border: none;
	outline: none;
	font-family: Josefin Sans, sans-serif;
	font-size: 20px;
	font-weight: bold;
	color: unset;
	display: flex;
	align-items: center;
	align-self: center;
	cursor: unset;
}
div.accountButton {
	padding: 0 20px;
	border-radius: 15px;
	display: flex;
	cursor: pointer;
}
.accountButton.remove {
	background-color: indianred;
}
.accountButton:hover {
	opacity: 0.68;
}

.notes {
    border-radius: 5px;
    justify-content: center;
    align-items: center;
	display: flex;
    background-color: hsla(0,0%,58.8%,.68);
	font-weight: 700;
	font-size: small;
	box-sizing: border-box;
	align-self: baseline;
	padding: 10px 40px;
	height: 100%;
	
}
.sync {
	padding: 0 5px;
	box-sizing: border-box;
}

.auth {
	margin: 20px auto;
	width: max-content;
	display: grid;
	grid-template-columns: auto 200px;
	column-gap: 20px;
	row-gap: 20px;
}
.auth > a:global(.link):global(.button), .auth > div:global(.link):global(.button) {
	border-radius: 10px;
	display: grid;
	grid-template-columns: auto 1fr auto;
	column-gap: 10px;
	min-height: 40px;
}
.auth > div:global(.link):global(.button).null {
	background-color: grey!important;
	color: lightgrey;
	cursor: default;
}
.auth > div:global(.link):global(.button).null > img {
	filter: invert(0.8);
}
.authMessage {
	font-size: 12px;
}

/* Play */

.upcomingBattle {
	position: relative;
	margin-top: 40px;
}

.upcomingBattle > .stage {
	display: grid;
	column-gap: 20px;
	row-gap: 15px;
	grid-template-columns: 28.8% 1fr 5ch 1fr 28.8%;
	grid-template-rows: 20px 1fr auto;
	grid-template-areas:
		'wFlag wFlag . bFlag bFlag'
		'wName wName vs bName bName'
		'Buttons Buttons Buttons Buttons Info'
		'Buttons Buttons Buttons Buttons Info';
}
.upcomingBattle > .stage.reversed {
	grid-template-areas:
		'wFlag wFlag . bFlag bFlag'
		'wName wName vs bName bName'
		'Info Buttons Buttons Buttons Buttons'
		'Info Buttons Buttons Buttons Buttons';
}

.upcomingBattle .flag {
	position: relative;
	top: -50px;
	display: flex;
	justify-content: center;
}

.upcomingBattle .name {
	display: flex;
	text-align: center;
	align-items: center;
	justify-content: center;
	font-weight: 700;
	font-size: 24px;
	background-color: hsla(0,0%,58.8%,.68);
	border-radius: 20px;
	padding: 5px;
}
.upcomingBattle .vs {
	grid-area: vs;
	font-weight: 700;
	font-size: 30px;
	display: flex;
	padding: 0 20px;
	justify-content: center;
	align-items: center;
}

.upcomingBattle .buttons {
	display: grid;
	grid-auto-columns: auto;
    grid-auto-rows: auto;
    overflow: hidden;
    column-gap: 15px;
    row-gap: 15px;
    font-size: 1em;
}

.upcomingBattle .buttons > :global(.header-button) {
	border-radius: 10px;
	padding: 3px 5px;
	grid-row: 2;
}
.upcomingBattle .buttons > :global(.header-button) > * {
	margin: 0 5px;
}

.upcomingBattle .buttons > :global(.header-button).join {
	grid-row: 1;
	grid-column: 1 / 4;
}

.upcomingBattle div.null {
	cursor: default;
	background-color: lightgrey;
	color: grey;
}

.upcomingBattle .infoBox {
	grid-area: Info;
	display: grid;
	grid-template-columns: 50fr 68.1fr;
	grid-auto-rows: auto;
	column-gap: 10px;
	padding: 10px;
	background-color: rgb(150, 150, 150, 0.31);
	overflow: hidden;
	border-radius: 15px;
	font-size: 16px;
	align-items: center;
}

.upcomingBattle .clickButton {
	background-color: rgba(150, 150, 150, 0.4);
	display: flex;
	text-align: center;
    justify-content: center;
    align-items: center;
    text-decoration: underline;
    cursor: pointer;
    border-radius: 15px;
    background-color: rgba(150, 150, 150, 0.68);
    text-decoration: none;
    padding: 2px;
}

.darkMode {
	padding: 10px;
	display: flex;
	align-items: center;
}
.darkMode svg {
	font-size: 32px;
}