.container {
    max-width: 1180px;
	margin: 0 20px 50px;
	box-sizing: border-box;
	overflow: hidden;

	display: grid;
	column-gap: 25px;
	row-gap: 25px;
	grid-template-columns: 1fr 1fr;
	font-family: 'Josefin Sans', sans-serif;
}

.userTable {
	display: grid;
	grid-template-columns: auto 1fr;
	align-items: center;
}
.status {
	color: #43853d;
	display: flex;
	justify-content: center;
}
a.profile {
	border-radius: 15px;
	padding: 0 10px;
}

.title {
	font-size: xx-large;
	margin: 20px 0;
	display: grid;
	grid-auto-columns: auto;
	grid-auto-flow: column;
	column-gap: 20px;
	align-items: center;
	width: max-content;
}
:global(.light) {
	font-weight: 300;
}

.searchBar {
	display: flex;
	flex-direction: row;
	background-color: var(--col-accent2);
	align-items: center;
	align-content: center;
	box-sizing: border-box;
	border-radius: 10px;
	width: 100%;
	margin-bottom: 20px;
}

.searchBar > * {
	height: 100%;
	padding: 0 5px;
}

.searchBar * {
	background-color: transparent;
}

.searchBar form {
	padding: 2px 0;
}

.searchBar div, .searchBar form, .searchBar input {
	box-sizing: border-box;
	height: 100%;
	width: 100%;
}

.searchBar input, .searchBar input:focus {
	border: none;
	padding: 0;
	margin: 0;
}
