.header {
    top: 0px;

    width: 100%;
    height: 16vh;
    z-index: 3; /* On account on react-carousel */
	background-color: var(--col-accent2);
	border-bottom: 2px darkgrey solid;
	text-shadow: none;
	display: grid;
	position: fixed;
    grid-template-columns: 20vh 1fr;
    align-items: start;
    -webkit-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.2);
    -moz-box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.2);
    box-shadow: 0px 2px 5px 0px rgba(0,0,0,0.2);
	font-family: 'Josefin Sans', 'Helvetica Neue', sans-serif;
	transition: height 0.68s ease;
}

.buttonContainer {
    display: grid;
    grid-template-columns: 2fr repeat(3, 1fr) auto 1.68fr;
    grid-auto-flow: column;
	grid-area: 'name link link link link link';
	align-items: center;
	height: inherit;
}

.buttonContainer .button:first-child {
	padding: 5px;
}

@media only screen and (max-width: 700px) {
	.header {
		grid-template-columns: 100%;
	}
	.buttonContainer, div.header .nameWrapper {
		display: none;
	}
}

.logoWrapper, .logoWrapper > img {
	z-index: 1;
}
.logoWrapper {
	display: flex;
	justify-content: center;
	background-color: transparent!important;
}
.logoWrapper > img {
	max-width: unset;
	display: inline-block;
	position: static;
	transition: height 0.68s ease;
}

div.headerButton {
	height: inherit;
	box-sizing: border-box;
	border-bottom: transparent 2px solid;
	word-wrap: break-word;
	font-size: 1.5vw;
}
div.headerButton:hover {
	border-bottom: blue 2px solid;
}
div.headerButton.selected {
	border-bottom: gold 2px solid;
}

a.button {
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
	background-color: transparent;
	height: calc(100% - 2vh);
	margin: 1vh 30px;
	padding: 0 5px;
	box-sizing: border-box;
	border-radius: 15px;
	color: #43853d;
}

@media only screen and (max-width: 700px) {
    div.installer {
        flex-direction: column;
    }
    div.icons {
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(2, 1fr);
        grid-auto-flow: unset;
    }
    .header > .logoWrapper {
		display: flex;
		justify-content: center;
		background-color: transparent;
	}
	.logoWrapper > img {
		height: 100%;
	}
}

a.button.native {
	text-align: center;
	background-color: #43853d7e;
	margin: 1vh 10px;
}

.notifications {
	position: fixed;
	max-width: 400px;
	min-width: 200px;
	min-height: 200px;
	background-color: rgba(200, 200, 200, 0.95);
	margin-top: 10px;
	right: 15%;
	transition: top 0.68s ease, opacity 0.33s ease;
	border-radius: 15px;
	font-family: Josefin Sans, sans-serif;
	padding: 20px;
	box-sizing: border-box;
	z-index: 3;
}

/* Mobile */


.mobileMenu {
	font-family: Josefin sans, sans-serif;
	background-color: rgba(235, 235, 235, 0.95);
	position: fixed;
	width: 100%;
	height: 100%;
	display: grid;
	grid-auto-rows: auto;
	grid-template-columns: 80%;
	justify-content: center;
	transition: all 0.5s ease;
	padding-top: 12vh;
	box-sizing: border-box;
	z-index: 2;
	overflow: hidden;
}

.mobileMenu a.button {
	color: rgb(25, 25, 25);
    text-emphasis: none;
	text-decoration: none;
	border-bottom: rgba(255, 255, 255, 0.8) solid 2px;
	padding: 20px;
	box-sizing: border-box;
	border-radius: 0px;
}

.mobileMenu hr {
	background-color: white;
	width: 80%;
	height: 5px;
}

@media only screen and (max-width: 700px) {
    div.header {
        grid-template-columns: 80% 20%;
        grid-column-gap: 0%;
	}
	a.logoWrapper {
		margin-left: 20vw;
		box-sizing: border-box;
	}
}

.menuButton {
	display: flex;
	align-items: center;
	justify-content: center;
	font-size: 4vh;
	height: inherit;
}