body {
    box-sizing: border-box;
    font: 400 20px/1.5 'Roboto', "Source Sans Pro", "Open Sans", "San Francisco", Helvetica, Arial, sans-serif;
    color: #333;
    margin: 0;
	background-color: #fff;
	overflow-y: scroll;
}

#root {
	display: flex;
	justify-content: center;
	background-repeat: no-repeat;
	background-position: center;
	background-attachment: fixed;
	background-color: rgba(255, 255, 255, 0.1);
	max-width: 100vw;
	min-height: 100vh;
	overflow: hidden;
	--col-accent1: #43853d;
	--col-accent2: skyblue;
	--col-accent1-hover: white;
	--col-accent2-hover: #43853d;
	--col-accent4: #002147;
}
#root.dark {
	--col-accent1: #43853d;
	--col-light1: #fff;
	--col-accent1-hover: white;
	--col-accent2: #002147;
	--col-accent2-hover: rgb(190, 162, 0);
	--col-accent3: #fff;
	--col-accent4: #002147;
	--col-container1: white;
	background-color: #1e1e1e;
	color: rgba(240, 240, 240, 0.9);
	text-shadow: 0.5px 1px 1px rgba(0, 0, 0, .9);
}

header,
#main,
footer {
    display: -ms-flexbox;
    display: flex
}
#main {
    -ms-flex: 1 0 auto;
	flex: 1 0 auto;
	margin-top: 16vh;
	flex-direction: column;
	padding: 0px 20px;
	width: 100%;
	max-width: 1080px;
	min-height: 72vh;
	box-sizing: border-box;
}
#main * {
	scroll-margin: 8vh;
}
#main > section:last-of-type {
	margin-bottom: 80px;
}

.container {
	margin: 10px;
	color: var(--col-container1);
	background-color: rgba(200, 200, 200, 0.5);
	border-radius: 15px;
	padding: 10px;
	box-shadow: rgba(0, 0, 0, 0.4) 0px 2px 10px 0px;
	font-family: 'Josefin Sans', sans-serif;
	position: relative;
	transition: max-height 0.333s ease, padding-top 0.333s ease, padding-bottom 0.333s ease, margin-top 0.333s ease, margin-bottom 0.333s ease;
}
#root.dark .container {
	--col-accent1: #e8ffe6;
}
.container:empty {
	margin: 0px;
	padding: 0px;
}
.container.collapsed {
	max-height: 0px;
	padding-top: 0px;
	padding-bottom: 0px;
	margin-top: 0px;
	margin-bottom: 0px;
}
.container:not(.collapsed) {
	max-height: 100%;
}

.bodyContainer {
	border-radius: 25px;
}

.container:hover {
	top: -1px;
}

section {
	margin-top: 25px;
}

.icon {
    height: 40px;
}

.button-list {
	display: grid;
	grid-auto-columns: 1fr;
	grid-auto-flow: column;
	column-gap: 10px;
	row-gap: 10px;
	border-radius: 25px;
	overflow: hidden;
}

.header-button, .header-button:link, .header-button:active {
	display: flex;
	background-color: var(--col-accent2);
	justify-content: center;
	text-align: center;
	box-sizing: border-box;
	cursor: pointer;
}

.dark .header-button, .dark .button {
	box-shadow: 1px 2px 2px rgba(0, 0, 0, 0.3);
}

a.header-button:hover > *{
	background-color: var(--col-accent2-hover);
	color: var(--col-accent1-hover);
}

a.header-button.selected {
	background-color: cyan;
}

a.twitch-link.header-button:hover > *, .button-list > a.youtube-link.header-button:hover > * {
	animation: flash-yellow 0.5s linear infinite alternate;
}

@keyframes flash-yellow {
	from {
		background-color: var(--col-accent1);
	}
	to {
		background-color: transparent;
	}
}

a.header-button > div > *, div.header-button > div > * {
	padding: 0 20px;
}
.header-button > div > svg, .header-button > div > img {
	padding: 0 5px;
	height: 1em;
}


a.header-button.stream-link {
	display: flex;
}

a.header-button.stream-link > div {
	flex-direction: row;
	align-items: center;
}

.header-button.twitch-link {
	background-color: #6441a5;
	color: rgb(80, 0, 80);
}

a.header-button.youtube-link {
	background-color: lightgrey
}

@media only screen and (max-width: 700px) {
	a.header-button.stream-link {
		grid-column: unset;
	}
}

.header-button div {
	width: 100%;
	display: flex;
	align-items: center;
	justify-content: center;
}

.header-button > div.full-text {
	grid-column: 1 / span 2;
	display: grid;
	grid-template-rows: 68% 32%;
	width: 100%;
	height: unset;
	position: relative;
	padding: 10px 0;
}

.header-button > div.full-text > * {
	margin: 2px 0;
}

.header-button > div.full-text > *:first-child:last-child {
	grid-row: 1 / span 2;
}

.header-button > div.full-text > h5 {
	font-size: 14px;
}

a.header-button img {
	max-height: 40px;
}

.button {
    display: flex;
    background-color: rgb(123, 121, 255);
    border-radius: 4px;
    width: 100%;
    height: 100%;
    align-items: center;
    align-content: center;
    justify-content: center;
	line-height: 20px;
	color: var(--col-accent1-hover);
	user-select: none;
	-webkit-user-select: none;
	font-weight: bold;
	cursor: pointer;
}

.button:link, a.button:visited {
	color: var(--col-accent1-hover);
}

.button:hover:not(.sent) {
    color: var(--col-light1);
    background-color: var(--col-accent1);
}
.button:hover > img {
	filter: invert(1);
}

.button > svg {
    transition: transform 0.2s ease;
}

.button > * {
	padding: 10px 5px;
    height: 20px;
}

.iframe {
    width: 100%;
    min-height: 600px;
}

